* {
    font-family: 'Courier New', Courier, monospace;
}

body{
    padding: 60px;
}

input{
    width: 100%;
    font-size: 1.2rem;
    color: grey;
}



.card-grid {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: row;
}

.card{
    align-content: content;
    border: 1px solid grey;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
}

.card p {
    padding: 5px;
    text-align: center;
}

.card img{
    max-height: 230px;
}